body {
    margin: 0;
    background-color: #dfdfdf; }

.demo_page_wrap {
    width: 100vw;
    height: 100vh;
    min-height: 330px;
    display: flex;
    justify-content: center;
    align-items: center;
    .demo_page {
        max-width: 1200px;
        // max-width: 80vw
        // width: 80vw
        flex-basis: 1200px;
        height: 60vh;
        min-height: 320px;
        border: 1px solid #dbedff;
        box-shadow: 0px 0px 16px -2px rgba(3,47,98,0.4);
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        justify-content: stretch;
        .row_1, .row_3 {
            min-height: 25px;
            background-color: #dbedff;
            display: flex;
            align-items: center;
            justify-content: space-between; }
        .row_2 {
            flex-grow: 1;
            display: flex;
            flex-wrap: wrap;
            height: 100%;
            overflow: auto;
            &::-webkit-scrollbar {
                width: .55%;
                min-width: 8px;
                height: 8px;
                background-color: #dbedff; }
            &::-webkit-scrollbar-thumb {
                background-color: #032f62; }
            &::-webkit-scrollbar-track {
                background-color: #dbedff; } }
        .row {
            label {
                padding: 3px 15px;
                cursor: pointer; } } } }

.demo_page {
    .row_2 {
        .container_settings_wrap,
        .push_settings_wrap {
            flex-grow: 1;
            width: 100%;
            flex-basis: 450px;
            .header {
              display: flex;
              justify-content: center;
              height: 20px;
              background-color: #032f62;
              color: #dbedff;
              padding: 5px 0; }
            .settings_wrap {
              overflow: auto;
              height: calc(100% - 30px);
              &::-webkit-scrollbar {
                  width: .55%;
                  min-width: 8px;
                  height: 8px;
                  background-color: #dbedff; }
              &::-webkit-scrollbar-thumb {
                  background-color: #032f62; }
              &::-webkit-scrollbar-track {
                  background-color: #dbedff; }
              .settings {
                  min-height: 700px; } } } } }


.demo_page {
    .settings {
        margin: 0;
        padding: 0;
        list-style-type: none;
        li:nth-child(odd) {
            background-color: #fffbdd;
            .description {
                &:after {
                    border-color: transparent transparent #fffbdd transparent; } } }
        li {
            padding: 10px;
            &.disabled {
                .prop_name,
                .prop_val {
                    user-select: none;
                    opacity: .5; } }
            .params {
                display: flex; } } } }

.demo_page {
    .settings {
        .prop_name {
            min-width: 150px;
            display: inline-block; }
        .prop_val {
            outline: 1px solid #dbedff;
            flex-grow: 1;
            padding: 0 10px;
            &:focus {
                outline: 1px solid #165c26; } }
        .description {
            margin: 15px 10px 5px;
            padding: 10px;
            border: 1px solid #000;
            border-radius: 5px;
            position: relative;
            &:before {
                content: '';
                display: block;
                position: absolute;
                left: 15px;
                top: -10px;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 0 10px 10px 10px;
                border-color: transparent transparent #000 transparent; }
            &:after {
                content: '';
                display: block;
                position: absolute;
                left: 15px;
                top: -9px;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 0 10px 10px 10px;
                border-color: transparent transparent #dfdfdf transparent; } } } }
